import React , { useState } from 'react'
import {AutoCenter, Form, Input, NavBar, Checkbox, Space,DatePicker, List, TextArea,Button, Toast } from 'antd-mobile'
import { useNavigate } from 'react-router-dom'
import dayjs from 'dayjs'

import axios from 'axios'
const now=new Date()
export default function ContractDetail() {
    const nav=useNavigate()
    const [form]=Form.useForm()
    const back=()=>{
        nav('/tabs/Shops')
    }
    const handleClick= async ()=>{
      //
      const values=form.getFieldValue()
      var formData=new FormData()
      let msg={
        'userId':values.userName,
        'shopId':values.shopName,
        'balance':parseInt(values.bookMoney),
        'text':values.otherItems
      }
      for(const key in msg){
        formData.append(key,msg[key])
      }
      // await axios.post('http://1.12.238.107:8080/card/create',{
      //   userId:values.userName,
      //   shopId:values.shopName,
      //   balance:parseInt(values.bookMoney),
      //   text:values.otherItems
      // })
      await axios({
        method:'POST',
        url:'http://1.12.238.107:8080/card/create',
        data:formData
      })
      .then((response)=>{
        console.log(response)
        Toast.show('')
      }).catch((error)=>{
        console.log(error)
      })
      // await axios.get('http://1.12.238.107:8080/init',{})
      // .then((response)=>{
      //   console.log(response)
      //   Toast.show('')
      // }).catch((error)=>{
      //   console.log(error)
      // })
      back()
      // console.log(values)
    }
  return (
    <div>
      <NavBar onBack={back} 
      style={{'--border-bottom':'1px #eee solid'}}></NavBar>
      <AutoCenter><span style={{fontSize:'x-large',fontWeight:'bold',margin:'5px'}}></span></AutoCenter>
      <Form requiredMarkStyle='asterisk'
      form={form} 
      initialValues={{
        userName:'',
        shopName:'',
        bookMoney:'',
        otherItems:''
      }}
      footer={
        <Button block type='submit' color='primary' size='large'onClick={handleClick}>
            
          </Button>
      }>
        <Form.Header></Form.Header>
        <Form.Item name='userName' label='()' rules={[{required:true}]}>
            <Input placeholder=''/>
        </Form.Item>
        <Form.Item name='userTel' label='()' rules={[{required:true}]}>
            <Input placeholder=''/>
        </Form.Item>
        <Form.Item name='shopName' label='()' rules={[{required:true}]}>
            <Input placeholder=''/>
        </Form.Item>
        <Form.Item name='shopTel' label='()' rules={[{required:true}]}>
            <Input placeholder=''/>
        </Form.Item>
        <Form.Item>
            <span></span>
        </Form.Item>
        <Form.Header> </Form.Header>
        <Form.Item name='type' label=''rules={[{required:true}]}>
                <Checkbox value='1'></Checkbox>
                <Checkbox value='2'></Checkbox>
        </Form.Item>
        <Form.Item  name='useScale' label=''rules={[{required:true}]}>
            <Input defaultValue=''/>
        </Form.Item>
        <Form.Item name='beginTime'label=''
        trigger='onConfirm'
        onClick={(e, datePickerRef) => {
            datePickerRef.current?.open()
          }}
          rules={[{required:true}]}>
        <DatePicker>
            {value =>
              value ? dayjs(value).format('YYYY-MM-DD') : ''
            }
          </DatePicker>
        </Form.Item>
        <Form.Item name='endTime'label=''
        trigger='onConfirm'
        onClick={(e, datePickerRef) => {
            datePickerRef.current?.open()
          }}
          rules={[{required:true}]}>
        <DatePicker>
            {value =>
              value ? dayjs(value).format('YYYY-MM-DD') : ''
            }
          </DatePicker>
        </Form.Item>
        <Form.Item name='shopScale'label=''rules={[{required:true}]}>
            <Checkbox value='1'></Checkbox>
            <Checkbox value='2'></Checkbox>
        </Form.Item>
        <Form.Item name='services'label=''rules={[{required:true}]}>
            <Input/>
        </Form.Item>
        <Form.Header> </Form.Header>
        <Form.Item name='bookMoney'label=''rules={[{required:true}]}>
            <Input/>
        </Form.Item>
        <Form.Item name='discount' label=''rules={[{required:true}]}>
            <Input/>
        </Form.Item>
        <Form.Item name='actualCost'label=''rules={[{required:true}]}>
            <Input/>
        </Form.Item>
        <Form.Header> </Form.Header>
            <Form.Item>
                    <span style={{fontSize:'large'}}></span>
            </Form.Item>
            <Form.Header> </Form.Header>
            <Form.Item>
                <List>
                    <List.Item>
                    1.
                    </List.Item>
                    <List.Item>2.</List.Item>
                    <List.Item>3.</List.Item>
                    <List.Item>4.</List.Item>
                    <List.Item>5.</List.Item>
                    <List.Item>6.,,</List.Item>
                    <List.Item>7.</List.Item>
                    <List.Item>8.</List.Item>
                </List>
            </Form.Item>
            <Form.Header> </Form.Header>
            <Form.Item>
              <span></span>
            </Form.Item>
            <Form.Item label=''>
              <span>
              7
              </span>
            </Form.Item>
            <Form.Item label=''>
              <List header='1.'>
                <List.Item>
                
                </List.Item>
                <List.Item>
                
                </List.Item>
                <List header='2.1'>
                  <List.Item>
                  2
                  </List.Item>
                  <List.Item>
                  3
                  </List.Item>
                  <List.Item>
                  
                  </List.Item>
                </List>
              </List>
            </Form.Item>
            <Form.Item label=''>
              <List>
                <List.Item>
                
                </List.Item>
                <List.Item>
                1233
                <br/>
                ()
                </List.Item>
              </List>
            </Form.Item>
            <Form.Header></Form.Header>
            <Form.Item>
              <List>
                <List.Item>
                (1)	20%
                </List.Item>
                <List.Item>
                (2) *80%*()
                </List.Item>
              </List>
            </Form.Item>
            <Form.Item>
              <span style={{color:'red',fontSize:'large',fontWeight:'bold'}}>40%</span>
            </Form.Item>
            <Form.Header> </Form.Header>
            <Form.Item name='otherItems'rules={[{required:true}]}>
              <TextArea placeholder='' autoSize={{minRows:3,maxRows:5}}></TextArea>
            </Form.Item>
            <Form.Header> </Form.Header>
            <Form.Item>
              <List>
                <List.Item>
                1
                </List.Item>
                <List.Item>
                2
                </List.Item>
              </List>
            </Form.Item>
            <Form.Item>
            <span style={{fontSize:'x-large',fontWeight:'bold'}}></span>
            </Form.Item>
            <Form.Item name='consumerSign'label='' rules={[{required:true}]}>
              <Input placeholder=''/>
            </Form.Item>
            <Form.Item name='shopSign'label=''rules={[{required:true}]}>
              <Input placeholder=''/>
            </Form.Item>
            <Form.Item name='signDate'label=''rules={[{required:true}]}>
              <RenderChildrenDemo/>
            </Form.Item>
      </Form>

    </div>
  )
}

function RenderChildrenDemo() {
  const [visible, setVisible] = useState(false)
  return (
    <Space align='center'>
      <Button
        onClick={() => {
          setVisible(true)
        }}
      >
        
      </Button>
      <DatePicker
        visible={visible}
        onClose={() => {
          setVisible(false)
        }}
        defaultValue={now}
        max={now}
      >
        {value => value?.toDateString()}
      </DatePicker>
    </Space>
  )
}