import React from 'react'
import { NavBar,List,Form,TextArea, Button,Dialog,Toast } from 'antd-mobile'
import { useLocation, useNavigate } from 'react-router-dom'
import axios from 'axios'
import { useEffect } from 'react'
export default function CheckOut() {
    const nav=useNavigate()
    const Props=useLocation()
    // console.log(Props.state)
    const cid=Props.state.cardID
   var obj={0:1}
    //
    useEffect(()=>{
      async function FetchData(){
        await axios.get(`http://1.12.238.107:8080/card/one?cardId=${cid}`)
        .then(response=>{
          // console.log(response.data)
          // var obj1=response.data
          Object.assign(obj,response.data)
          // console.log(obj)
        }).catch(error=>{
          console.log(error)
        })
      }
      FetchData()
    })
    //
    const back=()=>{
        nav('/tabs/Cards')
    }
    const onFinish= async ()=>{
        const result = await Dialog.confirm({
            content: '?',
          })
          if (result) {
            axios.get(`http://1.12.238.107:8080/card/delete?cardId=${cid}`)
            .then(response=>{
              console.log(response)
            }).catch(error=>{
              console.log(error)
            })
            Toast.show({ content: '', position: 'bottom' })
            back()
          } else {
            Toast.show({ content: '...', position: 'bottom' })
          }

    }
    // console.log(obj.value)
  return (
    <div>
      <NavBar onBack={back}></NavBar>
      <List header='' mode='card'>
        <List.Item extra='gt1'></List.Item>
        <List.Item extra=''></List.Item>
        <List.Item extra='2022121'></List.Item>
        <List.Item extra='200'></List.Item>
      </List>
      <Form footer={
        <Button block type='submit' color='warning' size='large'></Button>
      }
      onFinish={onFinish}>
        <Form.Header></Form.Header>
        <Form.Item name='reason' >
            <TextArea 
            placeholder=''
            autoSize={{minRows:3,maxRows:5}}/>
        </Form.Item>
      </Form>
    </div>
  )
}
